import React from "react";
import { faColumns, faImage, faKeyboard, faBars, faEdit, faGlasses, faBook, faArrowLeft } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
//import { createWorker } from "tesseract.js";

import { arachne } from "./arachne";
import { Selector, parseHTML } from "./elements";

class Argos extends React.Component{
    constructor(props){
        super(props);
        this.state = {
            mounted: false,
            mode: "img",
            imgURL: null,
            fullTextEdit: false,
            scanLst: [],
            zoomFactor: 100,
            scanLstSelected: [],
            contentMode: "pages",
            scansInPath: [],
            allScansPresetSelection: [],
            allScansSelection: [],
            ocrReady: false,
            work: {},
            displayBibliography: false,
        };
        window.addEventListener("keyup", e=>{
            if(e.target.tagName!="TEXTAREA"&&e.keyCode===37&&this.state.cIndex>0){
                // left
                this.setState({cIndex: this.state.cIndex-1});
            } else if (e.target.tagName!="TEXTAREA"&&e.keyCode===39&&this.state.cIndex<this.state.scanLst.length-1){
                // right
                this.setState({cIndex: this.state.cIndex+1})
            }
        });
        /*this.worker = createWorker({logger: m => console.log(m)});
        (async ()=>{
            await this.worker.load();
            await this.worker.loadLanguage("lat");
            await this.worker.initialize("lat");
            this.setState({ocrReady: true});
        })();*/
        
    }
    render(){
        let contentMenuLst = [];
        if(this.state.contentMenu){
            let i = -1;
            for(const scan of this.state.scanLst){
                i ++;
                contentMenuLst.push(<li key={i}>{scan.full_text?<FontAwesomeIcon icon={faKeyboard} style={{fontSize: "10px", margin: "0 5px 1px 0"}} />:null}<a id={i} style={{color: this.state.scanLstSelected.includes(scan.id)?"red":"inherit"}} onClick={e=>{
                    this.setState({cIndex: parseInt(e.target.id), contentMenu: false});
                }}>{isNaN(scan.filename)?scan.filename:parseInt(scan.filename)}</a></li>);
            }
        }
        let mainStyle = {
            position: "relative",
            margin: "60px 40px 60px 40px",
            display: "grid",
            gridTemplateColumns: this.state.mode==="img"||this.state.mode==="text"?"1fr":"1fr 1fr",
            gridTemplateRows: "1fr",
            columnGap: "20px",
        }
        if(this.state.contentMenu){
            mainStyle.left = "150px";
        }
        let fullTxtBox = <i>Kein Fließtext verfügbar.</i>;
        if(this.state.txt&&!this.state.fullTextEdit){
            fullTxtBox = <span style={{fontSize: "18px"}} dangerouslySetInnerHTML={parseHTML(this.state.txt.replace(new RegExp(`${this.state.currentQuery}`, "g"), `<mark>${this.state.currentQuery}</mark>`).replace(/\n/g, "<br />"))}></span>;
        } else if(this.state.fullTextEdit){
            fullTxtBox = <textarea style={{outline: "none", resize: "none", width: "100%", height: "500px"}} value={this.state.txt?this.state.txt:""} onChange={e=>{this.setState({txt: e.target.value})}} onBlur={e=>{
                this.props.status("saving");
                arachne.scan.save({id: this.state.scanLst[this.state.cIndex].scan_id, full_text: e.target.value});
                this.props.status("saved");
            }}></textarea>;
        }
        let sideMenu = null;
        if(this.state.contentMode==="pages"){
            sideMenu = <div style={{padding: "20px"}}>
                {arachne.access("e_edit")?<FontAwesomeIcon className="editButton" style={{position: "absolute", top: "10px", right: "10px", fontSize: "15px"}} icon={faEdit} onClick={()=>{this.setState({contentMode: "edit"})}} />:null}
                <h4>Inhalt</h4>
                <ul style={{listStyleType: "none", margin: "0", padding: "0"}}>{contentMenuLst}</ul>
                <div style={{height: "70px"}}></div>
                <div className="mainColors" style={{position: "fixed", bottom: "42px", left: "0", width: "180px", borderTop: "0.5px solid gray", padding: "4px 5px"}}><input type="text" style={{margin: "3px 1px", padding: "0", boxShadow: "none", width: "170px"}} onKeyUp={async e=>{
                    if(e.keyCode===13){
                        const results = await arachne.scan_lnk.search([{c: "edition_id", o: "=", v: this.props.edition},{c: "full_text", o: "=", v: `*${e.target.value}*`}], {select: ["id"]});
                        this.setState({currentQuery: e.target.value, scanLstSelected: results.map(r => r.id)});
                    }
                }} /></div>
            </div>;
        } else if (this.state.contentMode==="edit"){
            let allScanLst = [];
            let i = 0;
            for(const allScan of this.state.scansInPath){
                i++;
                allScanLst.push(<div className="all_scans_item" key={i} id={allScan.id}>{allScan.filename}</div>);
            }
            sideMenu = <div style={{paddingBottom: "60px"}}>                
                <div style={{position: "sticky", top: "0px", backgroundColor: "white", marginBottom: "20px", borderBottom: "1px solid lightgray", padding: "5px 10px 15px 10px"}}>
                    <div style={{marginBottom: "30px"}}>
                        {arachne.access("e_edit")?<FontAwesomeIcon className="editButton" style={{position: "absolute", top: "10px", right: "10px", fontSize: "15px"}} icon={faEdit} onClick={()=>{this.setState({contentMode: "pages"})}} />:null}
                    </div>
                    {this.state.allScansSelection.length}
                    <input type="button" onClick={async ()=>{
                            this.props.status("saving");
                            // delete old links
                            if(this.state.scanLst.length>0){
                                await arachne.scan_lnk.delete(this.state.scanLst.map(i=>i.id));
                            }
                            // create new links
                            const newLinks = this.state.allScansSelection.map(i=>{return {edition_id: this.props.edition, scan_id: i}});
                            if(newLinks.length>0){await arachne.scan_lnk.save(newLinks);}                        
                            this.props.status("saved");
                            setTimeout(()=>{window.location.reload()},500);
                            //this.setState({contentMode: "pages", contentMenu: false});
                    }} value="speichern" style={{fontSize: "15px", float: "right"}} />
                </div>
                <Selector multiSelect={true} className="all_scans" selectCallback={
                    (item, selection)=>{
                        console.log("beep in Selector");
                        arachne.getScan(selection.currentId).then(img=>{
                            this.setState({imgURL: img, txt: ""});
                        }).catch(e=>{throw e});
                        this.setState({allScansSelection: selection.ids});
                    }
                } preset={this.state.allScansPresetSelection} >{allScanLst}</Selector>
            </div>;
        }
        let currentPage = "";
        if(this.state.scanLst.length-1>=this.state.cIndex&&this.state.scanLst[this.state.cIndex]!=null){
            currentPage = this.state.scanLst[this.state.cIndex].filename;
            try{currentPage=parseInt(currentPage);}
            catch{}
        }
        return <div>
            <main style={mainStyle}>
                {this.state.mounted&&this.state.contentMode==="pages"&&this.state.scanLst.length===0?`FEHLER: Keine Seiten für diese Ressource gefunden! Melden Sie die ID ${this.props.edition}.`:null}
                {this.state.imgURL&&(this.state.mode==="img"||this.state.mode==="split")&&<div style={{textAlign: "center"}}><img src={this.state.imgURL} style={{boxShadow: "0 2px 3px gray", width: this.state.mode==="img"?this.state.zoomFactor+"%":"100%"}}></img></div>}
                {(this.state.mode==="text"||this.state.mode==="split")&&<div style={{boxShadow: "0 2px 3px gray", padding: "20px 40px", position: "relative"}}>
                    <div className="minorTxt" style={{textAlign: "center", marginBottom: "30px"}}><span dangerouslySetInnerHTML={parseHTML(this.state.title)}></span><span style={{float: "right"}}>{this.state.page}</span></div>
                    
                    <div style={{textAlign: "justify"}}>{fullTxtBox}</div>{arachne.access("e_edit")?< FontAwesomeIcon icon={faEdit} style={{position: "absolute", "bottom": "10px", right: "10px", color: "gray"}} onClick={()=>{if(this.state.fullTextEdit){this.setState({fullTextEdit: false})}else{this.setState({fullTextEdit: true})}}} />:null}
                    </div>
                    }
                    {this.state.cIndex>0?<div className="leftArrow" onClick={()=>{this.setState({cIndex: this.state.cIndex-1})}}></div>:null}
                    {this.state.cIndex<this.state.scanLst.length-1?<div className="rightArrow"  onClick={()=>{this.setState({cIndex: this.state.cIndex+1})}}></div>:null}
            </main>
            {this.state.contentMenu?<div className="mainColors" style={{position: "fixed", top: "43px", left: "0", bottom: "0", width: "200px", boxShadow: "0 0 2px gray", overflow: "scroll"}}>
                {sideMenu}
            </div>:null}
            <header className="mainColors" style={{position: "fixed", top: "0", right: "0", left: "0", boxShadow: "0 0 2px gray", display: "grid", gridTemplateColumns:"1fr 1fr"}}>
                <div>
                    {false?<FontAwesomeIcon icon={faArrowLeft} style={{fontSize: "30px", margin: "5px 0 2px 25px"}} onClick={()=>{this.props.setSite("overview")}} />:null}
                    <FontAwesomeIcon icon={faBars} style={{fontSize: "30px", margin: "5px 0 2px 25px"}} onClick={()=>{this.state.contentMenu?this.setState({contentMenu: false, contentMode: "pages"}):this.setState({contentMenu: true})}} />
                    <div style={{position: "absolute", top: "10px", left: "120px"}}>{this.state.work.id>0?<span dangerouslySetInnerHTML={parseHTML(this.state.work.opus)}></span>:<span style={{color: "red", margin: "0px 25px 0px 0px"}}>Fehler: Die Ressource ist mit keinem Werk verknüpft!</span>}</div>
                </div>
                <div style={{textAlign: "right"}}>
                    {false&&this.state.work.id>0?<FontAwesomeIcon icon={faBook} style={{fontSize: "32px", margin: "0px 25px 5px 0px"}} onClick={async ()=>{if(this.state.displayBibliography){this.setState({displayBibliography: false})}else{this.setState({displayBibliography: true})}}} />:null}
                    {this.state.ocrReady?<FontAwesomeIcon icon={faGlasses} style={{fontSize: "25px", margin: "0px 25px 6px 0"}} onClick={async ()=>{
                        if(this.state.txt===null||window.confirm("Ein Volltext ist bereits verfügbar. Soll er überschrieben werden?")){
                            this.props.status("searching", "Text wird konvertiert...");
                            const { data: { text } } = await this.worker.recognize(this.state.imgURL);
                            this.setState({txt: text});
                            this.props.status("found", "Text hinzugefügt!");
                            //await worker.terminate();
                        }
                    }} />:null}
                    <FontAwesomeIcon icon={faImage} style={{fontSize: "35px", margin: "5px 25px 3px 0"}} onClick={()=>{this.setState({mode:"img"})}} />
                    <FontAwesomeIcon icon={faColumns} style={{fontSize: "30px", margin: "0 20px 5px 0"}} onClick={()=>{this.setState({mode:"split"})}} />
                    <FontAwesomeIcon icon={faKeyboard} style={{fontSize: "33px", margin: "0 30px 4px 0"}} onClick={()=>{this.setState({mode:"text"})}} />
                </div>
            </header>
            <footer className="mainColors" style={{position: "fixed", bottom: "0", right: "0", left: "0", boxShadow: "0 0 2px gray", display: "grid", gridTemplateColumns:"1fr 1fr", height: "43px"}}>
                <div style={{position: "relative", top: "1px", left: "10px"}}>
                    <input style={{position: "relative", top: "7px", marginRight: "10px", width:"400px"}} type="range" min="0" max={this.state.scanLst.length-1} value={this.state.cIndex} onChange={e=>{this.setState({cIndex: parseInt(e.target.value)})}} className="slider" />
                    <span>p. {currentPage}</span>
                </div>
                <div style={{textAlign: "right"}}>
                {this.state.mode==="img"?<div style={{position: "relative", top: "1px", right: "10px"}}>
                    <span>{this.state.zoomFactor}%</span>
                    <input style={{position: "relative", top: "7px", marginLeft: "10px", width:"400px"}} type="range" min="0" max="200" value={this.state.zoomFactor} onChange={e=>{this.setState({zoomFactor: parseInt(e.target.value)})}} className="slider" />
                </div>:null}
                </div>
            </footer>
            {this.state.displayBibliography?<div className="bibliographyBox">{this.state.work.bibliography}</div>:null}
        </div>;
    }
    componentDidMount(){
        const setupEdition = async () => {
            // get edition
            const edition = await arachne.edition.get({id: this.props.edition});
            if(edition.length===0){
                alert(`Ein Fehler ist aufgetreten: Die Ressource konnte nicht gefunden werden. Melden Sie die ID ${this.props.edition}`)
            }
            // get work
            let work = await arachne.work.get({id: edition[0].work_id});
            console.log(work);
            if(work.length===1){work = work[0]}
            else{work = {}}

            // get scans
            let scanLst = await arachne.scan_lnk.get({edition_id: this.props.edition});
            scanLst = scanLst.sort((a, b) => b.filename < a.filename);
            const title = edition.length===1?`${edition[0].opus.replace(" <cit></cit>", "")} (${edition[0].label})`:"";
            this.setState({mounted: true, title: title, path: edition[0].path, edition: edition[0], scanLst: scanLst, cIndex: scanLst.length-1, work: work});
        }
        setupEdition();
    }
    componentDidUpdate(prevProps, prevState){
        if(prevState.cIndex!=this.state.cIndex){
            // update current page!
            if(this.state.scanLst.length>0){
                arachne.getScan(this.state.scanLst[this.state.cIndex].scan_id).then(img=>{
                    this.setState({imgURL: img, txt: this.state.scanLst[this.state.cIndex].full_text, page: isNaN(this.state.scanLst[this.state.cIndex].filename)?this.state.scanLst[this.state.cIndex].filename:parseInt(this.state.scanLst[this.state.cIndex].filename)});
                }).catch(e=>{throw e});    
            }            
        }
        if(prevState.contentMode==="pages"&&this.state.contentMode==="edit"){
            const getAllScans = async () => {
                const scans = await arachne.scan.get({path: this.state.path});
                const newAllScansPresetSelection = this.state.scanLst.map(i=>i.scan_id);
                this.setState({scansInPath: scans, allScansPresetSelection: newAllScansPresetSelection, allScansSelection: newAllScansPresetSelection});
            }
            getAllScans();
        }
    }
}

export { Argos };